<template>
  <!-- Change the file name -->
  <v-dialog v-model="dialog" width="500">
    <v-card>
      <!-- Card Title -->
      <v-card-title class="headline teal lighten-4" primary-title></v-card-title>

      <!-- Card Content -->
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex>
              <v-text-field label="" v-model="newCatelogName" @keyup.enter="newCatelog"></v-text-field>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>

      <v-divider/>

      <!-- Card Action -->
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="pink lighten-1" flat @click="closeDialog">
          <strong></strong>
        </v-btn>
        <v-btn color="teal lighten-1" flat @click="newCatelog" >
          <strong></strong>
        </v-btn>
      </v-card-actions>

    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ['dialog'],
  data() {
    return {
      newCatelogName: ''
    }
  },
  methods: {
    newCatelog() {
      this.$emit('newCatelog', this.newCatelogName);
      this.closeDialog();
    },
    closeDialog() {
      this.$emit('closeDialog', false);
      this.newCatelogName="";
    }
  },
  created() {
    
  }
};
</script>
