<template>
  <v-container grid-list-xl pa-3>
    <v-layout class="data" align-center row wrap>
      <!--  -->
      <v-flex xs6 sm4 md1 class="order">{{ index + 1 }}</v-flex>

      <!--  -->
      <v-flex xs6 sm4 md2 class="item" id="item">{{ material.item }}</v-flex>

      <!--  -->
      <v-flex xs12 sm4 md3 class="number">
        <v-text-field
          type="number"
          class="inputPrice"
          value="1"
          label=""
          @change="updateQuantity"
        ></v-text-field>
      </v-flex>

      <!--  -->
      <v-flex xs12 sm4 md2 class="unit">
        <v-select :items="units" label="" :value="units[0]" attach @change="updateUnit"></v-select>
      </v-flex>

      <!--  -->
      <v-flex xs11 sm7 md3 class="unit">
        <v-textarea solo autofocus height="90" placeholder="" @change="updateNote"></v-textarea>
      </v-flex>

      <!--  -->
      <v-flex xs1 sm1 class="action" pa-0>
        <!-- Delete -->
        <v-btn flat icon color="indigo" @click="deleteMaterial(index)">
          <v-icon>delete</v-icon>
        </v-btn>
      </v-flex>

    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";

export default {
  props: ["material", "index"],
  methods: {
    ...mapActions(["deleteMaterial"]),
    updateQuantity(element) {
      const data = {
        index: this.index,
        quantity: element
      };
      this.$store.dispatch("update_quantity", data);
    },
    updateUnit(element) {
      const data = {
        index: this.index,
        unit: element
      };
      this.$store.dispatch("update_unit", data);
    },
    updateNote(element) {
      const data = {
        index: this.index,
        note: element
      };
      this.$store.dispatch("update_note", data);
    }
  },
  computed: {
    units() {
      return this.$store.getters.units;
    }
  }
};
</script>



<style lang="scss" scoped>
.data {
  border-bottom: 1px solid #c8c8c8;
}

.inputPrice input[type="number"] {
  -moz-appearance: textfield;
}
.inputPrice input::-webkit-outer-spin-button,
.inputPrice input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
</style>

